    margin-right: 0px;
    text-align: center;
  }
  .our-videos ul.nacc li {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  .our-videos .nacc .thumb iframe {
    height: 500px;
  }
  form#contact {
    margin-left: 0px;
    margin-top: 60px;
  }
  form#contact {
    padding: 45px 30px;
  }
  .footer-item {
    margin-bottom: 45px;
  }
  .subscribe-newsletters {
    margin-bottom: 0px;
  }
}

@media (max-width: 767px) {
  .faqcenter {
    margin: auto;
    width: 80%;
  }
  
  .main-banner .item .down-buttons .main-blue-button {
    margin-right: 0px;
    margin-bottom: 15px;
  }
  .main-banner .item .down-buttons .call-button a {
    margin-left: 0px;
  }
  .subscribe .inner-content:after {
    z-index: -1;
  }
  .subscribe .inner-content {
    padding: 60px 30px 100px 30px;
  }
  .subscribe .inner-content form input {
    width: 49%;
  }
  .subscribe .inner-content form button {
    width: 100%;
    background-color: #ff695f;
    margin-top: 30px;
  }
  .our-videos ul.nacc li {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  .contact-left-dec {
    display: none;
  }
  .contact-us .info {
    display: inline-block;
  }
  .contact-us .info span {
    margin-bottom: 20px;
  }
}

@media (max-width: 576px) {
  .road-item {
      margin-bottom: 15px;
  }
  iframe{
    width: 400!important
  }
  .main-banner{
    padding: 0 0 0 0;
  }
  .faqcenter {
    margin: auto;
    width: 80%;
  }
  .road-inner {
      flex-direction: column;
  }

  .road-inner > div {
      order: 2 !important;
  }

  .road-inner > div.road-image {
      order: 1 !important;
      padding-right: 0;
      margin-bottom: 20px;
  }

  .road-caption {
      text-align: center;
  }

  .inner-team-box {
      max-width: 210px;
  }
}


